/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.edgtf-plc-holder {
    @include edgtfRelativeHolderLayout();

    .edgtf-plc-outer {
        @include edgtfRelativeHolderLayout();
    }

    .edgtf-plc {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        &:hover {
            .edgtf-plc-inner {
                .edgtf-plc-text {
                    opacity: 1;
                }
            }
        }

        .touch & {
            cursor: pointer;
        }
    }

    .edgtf-plc-title {
        margin: 7px 0 0 0;
    }

    .edgtf-plc-category {
        margin: 0px;
    }

    .edgtf-plc-excerpt {
        margin: 22px 0 0 0;
    }

    .edgtf-plc-rating-holder {
        @include edgtfRelativeHolderLayout();

        line-height: 1;
        margin: 3px 0 6px;
    }

    .edgtf-plc-rating {
        @include edgtfWooCommerceRatingsStyle();
    }

    .edgtf-plc-price {
        margin-left: 11px;

        .amount {
            @include edgtfWooCommercePriceStyle();
        }

        del .amount {
            margin-right: 5px;
            text-decoration: line-through;
        }

        ins {
            .amount {
                margin-left: 5px;

                @include edgtfWooCommercePriceStyle('discount');
            }
        }
    }

    .edgtf-plc-add-to-cart {
        display: inline-block;
        font-size: 0px;
        vertical-align: middle;
        background: #fff;

        @include edgtfTransition(opacity 0.3s ease);

        .add_to_cart_button {
            @include edgtfWooCommerceButtonsStyle('\f370');
        }

        .edgtf-read-more-button {

        }

        .added {
            display: none;
        }

        .added_to_cart.wc-forward {
            @include edgtfWooCommerceButtonsStyle('\f13a');
        }
    }

    .edgtf-plc-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .edgtf-plc-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .edgtf-plc-onsale,
            .edgtf-plc-out-of-stock,
            .edgtf-plc-new-product {
                @include edgtfWooCommerceLabelStyle();
            }

            .edgtf-plc-new-product {
                @include edgtfWooCommerceLabelStyle('left');
            }
        }

        .edgtf-plc-text {
            bottom: 0;
            box-sizing: border-box;
            display: inline-block;
            left: 50%;
            opacity: 0;
            position: absolute;
            white-space: nowrap;
            z-index: 10;

            @include edgtfTransition(all .3s);
            @include edgtfTransform(translateX(-50%));
        }

        .edgtf-plc-text-inner {
            font-size: 0; //to remove space between inline-block items

            .edgtf-plc-category,
            .edgtf-plc-excerpt,
            .edgtf-plc-rating,
            .edgtf-plc-price {
                color: $default-text-color;
            }
        }

        .edgtf-plc-link {
            position: absolute;
            top: 0;
            left: 0;
            display: inline-block;
            width: 100%;
            height: 100%;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // standard

    &.edgtf-standard-layout {
        .edgtf-plc-inner {
            width: 100%;

            .edgtf-plc-text {
                background-color: $default-shading-color;
                border: $border-with-image-width solid transparent;
                height: 100%;
                left: 0;
                padding: 10px;
                top: 0;
                width: 100%;
                z-index: 8;

                @include edgtfBorderWithImage();
                @include edgtfTransform(none);

                .edgtf-plc-text-outer {
                    position: relative;
                    display: table;
                    width: 100%;
                    height: 100%;
                }

                .edgtf-plc-text-inner {
                    position: relative;
                    display: table-cell;
                    width: 100%;
                    height: 100%;
                    vertical-align: middle;
                    text-align: center;
                    font-size: unset;
                }

                .edgtf-plc-add-to-cart {
                    margin-top: 15px;
                }
            }

            // hover handling
            &:hover {
                .edgtf-plc-text {
                    opacity: 1;

                    @include edgtfTransition(all .6s cubic-bezier(0.12, 0.68, 0.43, 0.95));
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // simple

    &.edgtf-simple-layout {
        .edgtf-plc {
            display: table;
            padding-left: 0px;

            * {
                box-sizing: border-box;
            }
        }

        .edgtf-plc-inner {
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 0 0 30px;
            z-index: 1;

            @include edgtfTransform(translateZ(0));

            .edgtf-plc-buttons-wrapper-outer {
                @include edgtfTransition(all .6s cubic-bezier(0.12, 0.68, 0.43, 0.95));
            }

            // hover handling
            &:hover {
                .edgtf-plc-buttons-wrapper-outer {
                    opacity: 1;
                }
            }
        }

        .edgtf-plc-text-wrapper {
            position: relative;
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            z-index: 0;
        }

        .edgtf-plc-text {
            padding: 85px 80px 85px 70px;
        }

        .edgtf-plc-background-holder {
            height: 100%;
            position: absolute;
            right: 0;
            top: 0;
            width: 200%;
            background: #ffffff;

            @include edgtfBorderWithImage();
        }

        .edgtf-plc-buttons-wrapper-outer {
            @include edgtfAbsoluteHolderLayout();

            background: $default-shading-color;
            left: 30px;
            opacity: 0;
            width: calc(100% - 30px);
        }

        .edgtf-plc-buttons-wrapper {
            @include edgtfTableLayout();

            text-align: center;
        }

        .edgtf-plc-buttons-wrapper-inner {
            @include edgtfTableCellLayout();
        }

        .edgtf-plc-add-to-cart {
        }

        .edgtf-plc-text,
        .edgtf-plc-text-outer,
        .edgtf-plc-text-inner {
            @include edgtfRelativeHolderLayout();
            -webkit-transform: translateZ(0);
        }

        .owl-prev {
            left: 60px;
        }

        .owl-next {
            right: calc(50% + 30px) !important;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // navigation

    &.edgtf-plc-nav-light-skin {
        .owl-nav {
            .owl-prev,
            .owl-next {
                color: $header-light-color;

                &:hover {
                    color: $header-light-hover-color;
                }
            }
        }
    }

    &.edgtf-plc-pag-inside-slider {
        .owl-prev,
        .owl-next {
            @include edgtfTransform(translateY(-50%));
        }

        .owl-dots {
            position: absolute;
            left: 0;
            bottom: 10px;
            margin: 0;
        }
    }

    &.edgtf-plc-pag-light-skin {
        .owl-dots {
            .owl-dot {
                span {
                    background-color: $header-light-hover-color;
                }

                &:hover,
                &.active {
                    span {
                        background-color: $header-light-color;
                    }
                }
            }
        }
    }

}

/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */