/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-pl-holder {
    @include edgtfRelativeHolderLayout();

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // loader

    .edgtf-prl-loading {
        background: #ffffff;
        display: none;
        height: 100%;
        left: 0;
        margin-top: 85px;
        position: absolute;
        top: 0;
        top: 0;
        width: 100%;
        z-index: 3;

        .edgtf-prl-loading-msg {
            color: $first-main-color;
            display: inline-block;
            text-align: center;
            width: 100%;

            @include edgtfHeading5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // category filter

    .edgtf-pl-categories {
        padding-bottom: 44px;
        position: relative;
        text-align: center;
        width: 100%;

        ul {
            display: inline-block;
            list-style: none;
            margin: 0 auto;
            vertical-align: middle;

            li {
                display: inline-block;
                float: left;
                padding: 0 32px;
                position: relative;

                &:first-child {
                    padding-left: 0;
                }

                a {
                    font-size: 16px;
                    font-weight: 600;
                    position: relative;
                    text-transform: lowercase;

                    &:before {
                        background: currentColor;
                        content: "";
                        height: 2px;
                        left: -14px;
                        position: absolute;
                        top: 11px;
                        transform-origin: left;
                        width: 8px;

                        @include edgtfTransition(all .2s ease-out);
                        @include edgtfTransform(scale(0, 1));
                    }

                    &:hover,
                    &.active {
                        color: inherit;

                        &:before {
                            @include edgtfTransform(scale(1, 1));
                        }
                    }

                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // content

    .edgtf-pl-outer {
        clear: both;
        min-height: 85px;
        position: relative;
        z-index: 1;

        .edgtf-no-posts {
            margin: 20px 25px 40px 25px;
            text-align: center;
        }
    }

    .edgtf-pli {
        box-sizing: border-box;
        display: inline-block;
        padding: 0;
        position: relative;
        vertical-align: top;
        width: 100%;
        z-index: 100;

        &.edgtf-pli-animated {
            @keyframes slideInFromBottom {
                0% {
                    @include edgtfTransform(translate3d(0, 100px, 0));
                }
                100% {
                    @include edgtfTransform(translate3d(0, 0, 0));
                }
            }

            animation: 1.5s ease-out 0s 1 slideInFromBottom;
        }

        .edgtf-pli-title {
            margin: 0;
        }

        .edgtf-pli-category {
            margin: 0px;
        }

        .edgtf-pli-excerpt {
            margin: 0px;
        }

        .edgtf-pli-rating-holder {
            @include edgtfRelativeHolderLayout();

            line-height: 1;
            margin: 3px 0 6px;
        }

        .edgtf-pli-rating {
            @include edgtfWooCommerceRatingsStyle();
        }

        .edgtf-pli-price {
            .amount {
                @include edgtfWooCommercePriceStyle();
            }

            del .amount {
                margin-right: 5px;
                text-decoration: line-through;
            }

            ins {
                .amount {
                    margin-left: 5px;

                    @include edgtfWooCommercePriceStyle('discount');
                }
            }
        }

        .edgtf-pli-price {
            margin-top: 4px;
        }

        .edgtf-pli-add-to-cart {
            background-color: #fff;
            display: inline-block;
            font-size: 0px;
            vertical-align: middle;

            @include edgtfTransition(opacity .37s cubic-bezier(.12, .68, .43, .95));

            .add_to_cart_button {
                @include edgtfWooCommerceButtonsStyle('\f370');

                &.loading {
                    @include edgtfAnimation(adding-to-cart .5s linear infinite);
                }
            }

            .edgtf-read-more-button {
            }

            .added {
                display: none;
            }

            .added_to_cart.wc-forward {
                @include edgtfWooCommerceButtonsStyle('\f13a');
            }
        }
    }

    .edgtf-pli-inner {
        display: inline-block;
        overflow: hidden;
        position: relative;
        vertical-align: middle;

        .touch & {
            cursor: pointer;
        }

        .edgtf-pli-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .edgtf-pli-onsale,
            .edgtf-pli-out-of-stock,
            .edgtf-pli-new-product {
                @include edgtfWooCommerceLabelStyle();
            }

            .edgtf-pli-new-product {
                @include edgtfWooCommerceLabelStyle('left');
            }
        }

        .edgtf-pli-text {
            bottom: 0;
            box-sizing: border-box;
            display: inline-block;
            left: 50%;
            opacity: 0;
            position: absolute;
            white-space: nowrap;
            z-index: 10;

            @include edgtfTransition(all .37s cubic-bezier(0.12, 0.68, 0.43, 0.95));
            @include edgtfTransform(translateX(-50%));
        }

        .edgtf-pli-text-inner {
            font-size: 0; //to remove space between inline-block items

            .edgtf-pli-category,
            .edgtf-pli-excerpt,
            .edgtf-pli-rating,
            .edgtf-pli-price {
                color: $default-text-color;
            }
        }

        .edgtf-pli-link {
            display: inline-block;
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;

            @include edgtfTransition(all .37s cubic-bezier(.12, .68, .43, .95));
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // info on image

    &.edgtf-info-on-image {
        .edgtf-pli-inner {
            .edgtf-pli-text {
                background-color: $default-shading-color;
                border: $border-with-image-width solid transparent;
                height: 100%;
                left: 0;
                padding: 10px;
                top: 0;
                width: 100%;
                z-index: 8;

                @include edgtfTransform(none);

                .edgtf-pli-text-outer {
                    display: table;
                    height: 100%;
                    position: relative;
                    width: 100%;
                }

                .edgtf-pli-text-inner {
                    display: table-cell;
                    font-size: unset;
                    height: 100%;
                    position: relative;
                    text-align: center;
                    vertical-align: middle;
                    width: 100%;
                }

                .edgtf-pli-add-to-cart {
                    margin-top: 15px;
                }
            }

            // hover handling
            &:hover {
                .edgtf-pli-text {
                    opacity: 1;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // info below image

    &.edgtf-info-below-image {
        .edgtf-pli-text-wrapper.edgtf-no-rating-price {
            padding-bottom: 25px;
        }

        .edgtf-pli-buttons {
            position: absolute;
            text-align: center;
            top: 50%;
            width: 100%;
            z-index: 3;

            @include edgtfTransition(all .37s cubic-bezier(.12, .68, .43, .95));
            @include edgtfTransform(translateY(-50%));
        }

        .edgtf-pli-add-to-cart {
            display: inline-block;
            vertical-align: middle;
        }

        .edgtf-pli-buttons {
            opacity: 0;
        }

        .edgtf-pli-link {
            background-color: $default-shading-color;
            opacity: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // hover info below image
    &.edgtf-hover-info-below-image {
        .edgtf-pli {
            .edgtf-pli-text-wrapper.edgtf-no-rating-price {
                padding-bottom: 25px;
            }

            .edgtf-pli-buttons {
                opacity: 0;
                position: absolute;
                text-align: center;
                top: 50%;
                width: 100%;
                z-index: 3;

                @include edgtfTransition(all .37s cubic-bezier(.12, .68, .43, .95));
                @include edgtfTransform(translateY(-50%));
            }

            .edgtf-pli-add-to-cart {
                display: inline-block;
                vertical-align: middle;
            }

            .edgtf-pli-link {
                background-color: $default-shading-color;
                opacity: 0;
            }

            .edgtf-pli-text-wrapper {
                margin: 0px 0 12px;
                opacity: 0;

                @include edgtfTransition(opacity .37s cubic-bezier(.12, .68, .43, .95));
            }

            &:hover {
                .edgtf-pli-buttons,
                .edgtf-pli-text-wrapper {
                    opacity: 1;
                }
            }
        }

        &.edgtf-product-info-light {
            .entry-title a {
                color: #fff;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

    }

    .edgtf-pli-text-wrapper {
        @include edgtfRelativeHolderLayout();

        margin: 26px 0 12px;
        text-align: center;
    }

    // hover handling
    .edgtf-pli-inner {
        &:hover {
            .edgtf-pli-link,
            .edgtf-pli-buttons {
                opacity: 1;

                @include edgtfTransition(all .6s cubic-bezier(0.12, 0.68, 0.43, 0.95));
            }
        }
    }

    &.edgtf-product-info-light {
        .edgtf-pli-inner {
            .edgtf-pli-text-inner {
                .edgtf-pli-title,
                .edgtf-pli-category,
                .edgtf-pli-excerpt,
                .edgtf-pli-rating {
                    color: #fff;
                }

                .edgtf-pli-price {
                    color: #fff;

                    del {
                        color: #ccc;
                    }
                }
            }
        }
    }

    &.edgtf-product-info-hover-dark {
        &.edgtf-info-on-image {
            .edgtf-pli-inner {
                .edgtf-pli-text {
                    background-color: rgba(0, 0, 0, 0.8);
                    padding: 0;
                }
            }
        }
    }

    &.edgtf-product-info-dark {
        .edgtf-pli-inner {
            .edgtf-pli-text-inner {
                .edgtf-pli-title,
                .edgtf-pli-category,
                .edgtf-pli-excerpt,
                .edgtf-pli-rating {
                    color: $default-heading-color;
                }

                .edgtf-pli-price {
                    color: $default-heading-color;
                }
            }
        }
    }

    $columns_space_label: ('no', 'tiny', 'small', 'normal', 'large');
    $columns_space_width: (0, 5, 10, 15, 25);

    @for $i from 0 to length($columns_space_label) {
        &.edgtf-#{nth($columns_space_label,$i+1)}-space {
            $column_width: nth($columns_space_width, $i+1);
            $column_width_padding: $column_width * 2;

            .edgtf-pl-outer {
                @if ($column_width == 0) {
                    margin: 0;
                } @else {
                    margin: 0 -#{$column_width}px;
                }

                .edgtf-pli {
                    padding: 0 #{$column_width}px #{$column_width_padding}px;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // standard

    &.edgtf-standard-layout {
        .edgtf-pli {
            float: left;
        }

        &.edgtf-normal-space {
            margin: 0 0 -30px;
        }

        &.edgtf-small-space {
            margin: 0 0 -20px;
        }

        &.edgtf-tiny-space {
            margin: 0 0 -10px;
        }

        $standard_list_columns: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($standard_list_columns) {
            &.edgtf-#{nth($standard_list_columns,$i+1)}-columns {
                $column_width: 100%/($i+2);

                .edgtf-pli {
                    width: $column_width;

                    @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                        &:nth-child(#{$i+2}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // masonry

    &.edgtf-masonry-layout {
        .edgtf-pl-outer {
            //margin-top: 80px;
            opacity: 0;
            overflow: hidden; //because of the ajax loader
        }

        .edgtf-pl-sizer {
            width: 100%;
        }

        .edgtf-pl-gutter {
            width: 0;
        }

        .edgtf-pli-inner {
            height: 100%;
            width: 100%;

            .edgtf-pli-image {
                height: 100%;
                overflow: hidden;

                .edgtf-pli-image-overlay {
                    background-position: center;
                    background-size: cover;

                    @include edgtfAbsoluteHolderLayout();
                }
            }
        }

        $masonry_list_columns: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($masonry_list_columns) {
            &.edgtf-#{nth($masonry_list_columns,$i+1)}-columns {
                $column_width: 100%/($i+2);

                .edgtf-pli,
                .edgtf-pl-sizer {
                    width: $column_width;
                }

                .edgtf-pli.edgtf-woo-image-large-width-height,
                .edgtf-pli.edgtf-woo-image-large-width {
                    width: $column_width*2;
                }
            }
        }
    }

    ///////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // Animated hover

    .edgtf-pli-outer {
        height: 100%;
        position: relative;
        width: 100%;
    }

    &.edgtf-animated-hover {
        .edgtf-pl-outer {
            padding-top: 15px;

            .edgtf-pli-outer {
                background-color: #111;
                height: 100%;
                position: relative;
                width: 100%;

                .edgtf-pli-inner {
                    @include edgtfTransition(all .25s cubic-bezier(.12, .68, .43, .95));
                    @include edgtfTransform(translate3d(0px, -0px, 0));

                    &:hover {
                        @include edgtfTransform(translate3d(-11px, -11px, 0));
                    }
                }
            }
        }
    }

    &.edgtf-border-pattern {
        &.edgtf-info-on-image {
            .edgtf-pli-inner {
                .edgtf-pli-text {
                    @include edgtfBorderWithImage();
                }
            }
        }
    }
}

/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */