////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// product list buttons

@mixin edgtfWooCommerceButtonsStyle($icon) {

    @include keyframes(adding-to-cart) {
        0%, 100% {
            opacity: 1;
        }
        50% {
            opacity: 0.7;
        }
    }

    border: none;
    display: inline-block;
    float: left;
    font-size: 0;
    height: $product-list-button-size;
    margin: 0 !important;
    padding: 0;
    position: relative !important;
    width: $product-list-button-size;
    z-index: 3;

    &:before {
        color: $default-heading-color;
        content: "#{$icon}";
        display: block;
        float: left;
        font-family: 'IonIcons';
        font-size: 23px;
        height: $product-list-button-size;
        line-height: $product-list-button-size;
        text-align: center;
        width: $product-list-button-size;

        @include edgtfTransitionTransform(.2s ease);
    }

    &:after {
        background: $default-border-color;
        content: "";
        display: block;
        height: 40px;
        left: -1px;
        position: absolute;
        top: 8px;
        width: $default-border-width;
    }

    &.yith-wcqv-button:after {
        display: none;
    }

    span {
        display: none;
    }

    &.loading {
        &:before {
            @include edgtfAnimation(adding-to-cart 1s linear infinite);
        }
    }

    &:not(.yith-wcwl-wishlistaddedbrowse):not(.yith-wcwl-wishlistexistsbrowse):hover {
        &:before {
            @include edgtfTransition(all .2s cubic-bezier(0.12, 0.68, 0.43, 0.95));
            @include edgtfTransform(translateY(-3px));
        }
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// labels

@mixin edgtfWooCommerceLabelStyle($position:right) {
    @if $position=='right' {
        background: $default-heading-color;
        left: auto;
        padding: 0 24px 0 16px;
        right: 0;

        &:before {
            border-color: transparent $default-heading-color transparent transparent;
            border-width: 0 20px 28px 0;
            left: -20px;
            right: auto;
        }
    } @else {
        background: $first-main-color;
        left: 0;
        padding: 0 16px 0 24px;
        right: auto;

        &:before {
            border-color: $first-main-color transparent transparent transparent;
            border-width: 28px 20px 0 0;
            left: auto;
            right: -20px;
        }
    }

    box-sizing: border-box;
    color: #ffffff;
    display: block;
    line-height: 28px;
    position: absolute;
    text-align: center;
    top: 0;
    width: auto;
    z-index: 100;

    @include edgtfTransform(translateZ(0));

    &:before {
        border-style: solid;
        content: "";
        display: block;
        height: 0;
        position: absolute;
        top: 0;
        width: 0;
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// price

@mixin edgtfWooCommercePriceStyle($type:regular) {
    @if $type=='regular' {
        background-color: $default-heading-color;

        &:before {
            border-color: transparent transparent $default-heading-color transparent;
            border-width: 0 0 20px 9px;
            left: -9px;
        }

        &:after {
            border-color: transparent transparent transparent $default-heading-color;
            border-width: 20px 0 0 9px;
            right: -9px;
        }
    } @else {
        background-color: $first-main-color;

        &:before {
            border-color: transparent $first-main-color transparent transparent;
            border-width: 0 9px 20px 0;
            left: -9px;
        }

        &:after {
            border-color: $first-main-color transparent transparent transparent;
            border-width: 20px 9px 0 0;
            right: -9px;
        }
    }

    box-sizing: border-box;
    color: #ffffff;
    display: inline-block;
    font-size: 14px;
    line-height: 20px;
    padding: 0 7px;
    position: relative;
    text-align: center;
    vertical-align: middle;

    &:before {
        border-style: solid;
        content: "";
        display: block;
        height: 0;
        position: absolute;
        top: 0;
        width: 0;
    }

    &:after {
        border-style: solid;
        content: "";
        display: block;
        height: 0;
        position: absolute;
        top: 0;
        width: 0;
    }

    ins {
        text-decoration: none;
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// star rating

%star-ratings {
    font-family: "Ionicons";
    font-size: 14px;
    line-height: 1.1;
    letter-spacing: 9px;
    color: $default-border-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

}

@mixin edgtfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;

    &:before {
        color: $default-border-color;
        content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
    }

    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        &:before {
            color: $default-heading-color;
            content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        }
    }
}

@mixin edgtfWooCommerceSingleProductRatingsStyle() {
    &:before {
        content: "\f4b3";
        position: relative;
        display: inline-block;
        vertical-align: middle;

        @extend %star-ratings;

        overflow: hidden;
        z-index: -1;
    }

    &.active {
        &:before {
            color: transparent;
        }

        &:after {
            content: "\f4b3";
            color: $default-text-color;
            display: inline-block;
            left: 0;
            position: absolute;
            top: 5px;
            vertical-align: middle;

            @extend %star-ratings;

            overflow: hidden;
            z-index: 1;
        }
    }
}

@mixin edgtfWooCommerceSingleProductPriceStyle() {
    @include edgtfHeading3();

    del {
        color: $default-border-color;
        margin-right: 13px;
        text-decoration: line-through;
    }

    ins {
        text-decoration: none;
    }
}