/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.edgtf-woo-single-page {
    &.edgtf-woo-single-thumb-on-left-side {
        .edgtf-single-product-content {
            .images {
                @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                    figure {
                        width: 100%;
                        margin: 0;
                    }

                    .woocommerce-product-gallery__image {
                        &:first-child {
                            left: 120px;
                            width: calc(100% - 120px);
                        }
                    }

                    &.woocommerce-product-gallery--with-images {
                        .woocommerce-product-gallery__image {
                            &:not(:first-child) {
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100px;
                                height: calc(25% + 5px);
                                padding: 0 0 20px;
                                box-sizing: border-box;

                                a {
                                    height: 100%;

                                    img {
                                        height: 100%;
                                    }
                                }
                            }

                            $thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
                            @for $i from 0 to length($thumbnail_numbers) {
                                $thumbnail_number_value: nth($thumbnail_numbers, $i+1);

                                &:nth-child(#{$thumbnail_number_value}#{n}) {
                                    $first_param: (25 * $i)#{'%'};
                                    $second_param: (5 * $i)#{px};
                                    $topPos: calc(#{$first_param} + #{$second_param});

                                    top: $topPos;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    &.edgtf-woo-single-thumb-below-image {
        .edgtf-onsale,
        .edgtf-out-of-stock {
            right: 10px;
        }
    }

    .edgtf-single-product-summary {
        .yith-wcwl-add-to-wishlist {
            background-color: transparent;
            float: left;
            width: auto;

            .yith-wcwl-add-button {
                a {
                    &:after {
                        content: "\f141";
                        float: left;
                        font-family: "Ionicons";
                        font-size: 17px;
                        line-height: 21px;
                        padding-right: 10px;
                    }
                }
            }
        }
    }

    .product {
        @include edgtfRelativeHolderLayout();

        float: none;
        margin: 0 0 88px;
    }

    .edgtf-single-product-content {
        @include edgtfRelativeHolderLayout();

        clear: both;
        margin: 0;

        .images {
            box-sizing: border-box;
            display: inline-block;
            float: left;
            margin: 0;
            padding: 0 27px 0 0;
            position: relative;
            vertical-align: middle;
            width: 57%;

            figure {
                display: inline-block;
                margin: 0 -10px;
                position: relative;
                vertical-align: top;

                a {
                    @include edgtfRelativeHolderLayout();

                    img {
                        display: block;
                        position: relative;
                        width: 100%;
                    }
                }
            }

            .woocommerce-product-gallery__trigger {
                height: 30px;
                line-height: 30px;
                position: absolute;
                right: 39px;
                text-align: center;
                top: 10px;
                width: 30px;
                z-index: 10;
            }

            .woocommerce-product-gallery__image {
                box-sizing: border-box;
                display: inline-block;
                margin: 0;
                padding: 0 10px;
                position: relative;
                vertical-align: top;

                &:first-child {
                    left: 10px;
                    padding: 0;
                    width: calc(100% - 20px);
                }

                .zoomImg {
                    cursor: crosshair;
                }
            }

            &.woocommerce-product-gallery--with-images {

                .woocommerce-product-gallery__image {

                    &:not(:first-child) {
                        float: left;
                        padding-top: 20px;
                    }
                }
            }

            $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
            @for $i from 0 to length($thumb_3_columns_label) {
                &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                    $column_width: 100%/($i+2);

                    .woocommerce-product-gallery__image {

                        &:not(:first-child) {
                            width: $column_width;
                        }
                    }
                }
            }
        }
    }

    .edgtf-single-product-summary {
        box-sizing: border-box;
        display: inline-block;
        float: left;
        padding: 0 0 0 27px;
        position: relative;
        vertical-align: middle;
        width: 43%;

        .edgtf-single-product-title {
            display: block;
            margin: 0;
        }

        .price {
            @include edgtfWooCommerceSingleProductPriceStyle();

            margin: 15px 0 0;
        }

        .woocommerce-product-rating {
            @include edgtfRelativeHolderLayout();

            line-height: 1;
            margin: 43px 0 0;
        }

        .woocommerce-product-details__short-description {
            @include edgtfRelativeHolderLayout();

            margin: 19px 0 0;

            p {
                margin: 0;
            }
        }

        .product_meta {
            margin: 31px 0 0;

            > span {
                display: block;
                margin: 8px 0;
                text-transform: lowercase;
                color: #0d0d0d;

                @include edgtfHeading5();

                .sku,
                a {
                    color: $additional-text-color;
                    display: inline-block;

                    &:first-child {
                        margin-left: 21px;
                    }
                }

                a:hover {
                    color: $first-main-color;
                }
            }
        }

        .edgtf-woo-social-share-holder {
            @include edgtfRelativeHolderLayout();

            margin: 0;

            > span {
                @include edgtfHeading5();

                display: inline-block;
                margin-right: 21px;
            }
        }

        p.stock.out-of-stock,
        p.stock.in-stock {
            color: $default-heading-color;
            font-weight: bold;
            margin: 18px 0 0;
        }

        .cart {
            @include edgtfRelativeHolderLayout();

            margin: 43px 0 0;
        }

        .edgtf-quantity-buttons {
            margin: 0 20px 0 0;
        }
    }

    .yith-wcwl-add-to-wishlist {
        margin-top: 23px;
    }

    .woocommerce-tabs {
        @include edgtfRelativeHolderLayout();

        margin: 120px 0 0;

        ul.tabs {
            @include edgtfRelativeHolderLayout();

            clear: both;
            list-style: none;
            margin: 0;
            padding: 0;

            > li {
                float: left;
                margin-right: 10px;
                padding: 0;

                a {
                    @include edgtfHeading5();

                    background-color: $additional-background-color;
                    box-sizing: border-box;
                    color: $default-heading-color;
                    display: inline-block;
                    padding: 14px 30px;
                    position: relative;
                    vertical-align: middle;
                }

                &.active a {
                    background-color: $default-heading-color;
                    color: $default-background-color;
                }

                &:not(.active) a {
                    //@include edgtfButtonOutlineColor($default-border-color);

                    &:hover {
                        background-color: $default-heading-color;
                        color: $default-background-color;
                    }
                }

                &:last-child {
                    margin-right: 0;
                }
            }
        }

        .entry-content {
            @include edgtfRelativeHolderLayout();

            > h2 {
                display: none;
            }

            > p {
                margin: 22px 0 0;
            }
        }

        table {
            margin: 22px 0 0;

            tr, th {
                border: 0;
            }

            td, th {
                padding: 0;
                text-align: left;
                vertical-align: top;

                p {
                    margin: 0;
                }
            }

            th {
                @include edgtfHeading5();

                padding: 0 10px 0 0;
                width: 1%;
            }

            td {
                width: 99%;
            }
        }

        #reviews {
            @include edgtfRelativeHolderLayout();
            margin: 22px 0 0;

            h2 {
                @include edgtfHeading4();

                margin-top: 0;
            }

            ol.commentlist {
                list-style: none;

                li {
                    margin: 0 0 15px;

                    &:last-child {
                        margin: 0;
                    }
                }

                .comment_container {
                    @include edgtfRelativeHolderLayout();

                    img {
                        float: left;
                        width: 70px;
                    }
                }

                .comment-text {
                    padding: 0 0 0 90px;

                    p {
                        margin: 0;
                    }
                }
            }

            .comment-respond {
                margin-top: 15px;

                .comment-reply-title {
                    margin: 0 0 10px;
                }

                p {
                    margin: 0;
                }

                .stars {
                    overflow: hidden;

                    a {
                        color: transparent;
                        display: inline-block;
                        font-size: 0;
                        margin: 0;
                        position: relative;
                        @include edgtfWooCommerceSingleProductRatingsStyle();
                    }
                }
            }
        }
    }

    .related.products,
    .upsells.products {
        margin: 120px 0 0;

        > h2 {
            display: block;
            margin: 0 0 50px;
            text-align: center;
        }

        .product {
            .edgtf-pl-text-wrapper {
                margin-bottom: 0;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */