/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.edgtf-woo-pagination-holder {
    @include edgtfRelativeHolderLayout();
    margin: 0 0 66px;
    clear: both;

    .edgtf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0;

            a,
            span {
                position: relative;
                display: inline-block;
                width: auto;
                margin: 0 12px;
                padding: 0;
                vertical-align: top;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                color: $additional-text-color;
                @include edgtfTransition(color .2s ease-out);

                &:hover {
                    color: $first-main-color;
                }

                &.current {
                    text-decoration: underline;
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    line-height: inherit;
                    margin: 0 12px;

                    &:before {
                        position: relative;
                        display: block;
                        font-family: 'Ionicons';
                        font-size: 11px;
                        line-height: inherit;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }

                &.prev {

                    &:before {
                        content: "\f107";
                    }
                }

                &.next {

                    &:before {
                        content: "\f10a";
                    }
                }
            }
        }
    }
}

.edgtf-woo-view-all-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 1%;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0;
        color: $additional-text-color;

        &:before {
            content: '|';
            padding: 0 12px 0 0;
            color: inherit;
        }

        &:hover {
            color: $first-main-color;
        }
    }
}

/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */