/* ==========================================================================
   WooCommerce Track Order page styles - begin
   ========================================================================== */

.edgtf-woocommerce-page.edgtf-woocommerce-order-tracking {

    .woocommerce {
        margin: 0 0 45px;

        > .track_order {
            display: block;
            margin: 0 0 60px;

            > p:not(.form-row),
            .clear {
                display: none;
            }

            .form-row {
                box-sizing: border-box;
                float: left;
                margin: 0;
            }

            .form-row-first,
            .form-row-last {
                margin-right: 7px;
                width: calc(40% - 7px);

                label {
                    display: none;
                }
            }

            .form-row:not(.form-row-first):not(.form-row-last) {
                width: 20%;
            }

            .button {

            }
        }
    }
}

/* ==========================================================================
   WooCommerce Track Order page styles - end
   ========================================================================== */