/* ==========================================================================
   Product Info shortcode styles - begin
   ========================================================================== */

.edgtf-product-info {
    position: relative;
    display: inline-block;
    vertical-align: middle;

    > div {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        p {
            margin: 0;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 9px 0 0;

            &:last-child {
                margin: 0;
            }
        }
    }

    .edgtf-pi-title,
    .edgtf-pi-image,
    .edgtf-pi-rating {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
    }

    .edgtf-pi-image {

        img {
            display: block;
        }
    }

    .edgtf-pi-category {

        h1, h2, h3, h4, h5, h6 {
            margin: 0;

            a {
                vertical-align: top;
            }
        }
    }

    .edgtf-pi-rating {
        @include edgtfWooCommerceRatingsStyle();
    }

    .edgtf-pi-add-to-cart {

        .edgtf-btn.edgtf-btn-solid {

            &.edgtf-white-skin {
                color: $default-heading-color;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff;
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }

            &.edgtf-dark-skin {
                color: #fff;
                background-color: $default-heading-color;
                border-color: $default-heading-color;

                &:hover {
                    color: $default-heading-color;
                    background-color: #fff;
                    border-color: #fff;
                }
            }
        }
    }
}

/* ==========================================================================
   Product Info shortcode styles - end
   ========================================================================== */