/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.edgtf-woocommerce-page {

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        background-color: transparent;
        border: 2px solid $default-border-color;
        box-sizing: border-box;
        display: block;
        line-height: 52px;
        list-style: none;
        margin: 0 0 30px;
        padding: 20px 30px;
        position: relative;
        text-transform: lowercase;
        width: 100%;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    .woocommerce-info {
        .showcoupon {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */