/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    height: 100%;

    @include edgtfOpenerStyle();

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        display: table;
        height: 100%;
        position: relative;
    }

    .edgtf-header-cart {
        display: table-cell;
        position: relative;
        vertical-align: middle;

        &:hover {
            color: inherit;
        }
    }

    &:hover {
        .edgtf-shopping-cart-dropdown {
            opacity: 1;
            top: calc(100% - 2px); // adjust due to button offset from opener style
            visibility: visible;
        }
    }

    .edgtf-cart-number {
        font-size: 11px;
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.edgtf-shopping-cart-dropdown {
    background-color: #fff;
    box-shadow: $menu-box-shadow;
    box-sizing: border-box;
    left: auto;
    opacity: 0;
    position: absolute;
    right: 0;
    text-align: left;
    top: calc(100% + 50px);
    visibility: hidden;
    width: 253px;
    z-index: 1000;

    @include edgtfTransition(all 0.35s cubic-bezier(.53, .01, .18, 1));

    ul {
        margin: 28px 28px 16px 28px;
        padding: 0;

        li {
            @include edgtfTableLayout();

            box-sizing: border-box;
            padding: 0 0 10px 0;

            &.edgtf-empty-cart {
                border-bottom: 0;
                color: $default-heading-color;
                font-size: 14px;
                font-weight: 300;
                padding-bottom: 12px !important;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edgtf-item-image-holder {
        display: table-cell;
        height: 100%;
        position: relative;
        vertical-align: top;
        width: 81px;

        img {
            display: block;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();

        padding-left: 15px;
        vertical-align: middle;

        .edgtf-product-title {
            box-sizing: border-box;
            display: block;
            font-size: 14px;
            line-height: 1em;
            margin: 0;
            padding: 0 20px 5px 0;
        }

        .remove {
            font-size: 12px;
            line-height: 1;
            position: absolute;
            right: 0;
            top: -1px;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity {
            color: $additional-text-color;
            display: inline-block;
            font-size: 13px;
            font-weight: 300;
            vertical-align: middle;
        }

        .amount {
            color: $additional-text-color;
            display: inline-block;
            font-size: 13px;
            font-weight: 300;
            vertical-align: middle;
        }
    }

    .edgtf-cart-bottom {
        padding: 13px 0 0;

        .edgtf-subtotal-holder {
            font-weight: 300;
            margin-bottom: 16px;
            padding-bottom: 9px;
            position: relative;
            width: 100%;

            > * {
                color: $default-heading-color;
                display: inline-block;
                font-size: 14px;
                line-height: 20px;
                vertical-align: top;
            }

            &:after {
                background: $default-border-color;
                bottom: 0;
                content: "";
                display: block;
                height: 2px;
                left: -28px;
                position: absolute;
                width: calc(100% + 56px);
            }
        }

        .edgtf-view-cart,
        .edgtf-checkout {
            @include edgtfButtonDefaultStyle();
            @include edgtfButtonSimpleStyle();

            font-size: 14px;

            &:hover {
                @include edgtfButtonSimpleHoverStyle();
            }
        }

        .edgtf-view-cart {
            float: left;
        }

        .edgtf-checkout {
            float: right;
        }
    }
}

.edgtf-vertical-menu-area {
    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}

/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */