/* ==========================================================================
   YITH Quick View styles - begin
   ========================================================================== */
#yith-quick-view-modal,
.yith-quick-view.yith-modal {
    .yith-quick-view-overlay {
        background-color: $default-shading-color;
    }

    .yith-wcqv-wrapper {
        padding: 0;
        max-width: 1080px;
        height: auto !important;
    }

    .yith-wcqv-main {
        @include edgtfRelativeHolderLayout();

        padding: 0;
        overflow: hidden !important;
        box-shadow: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // product stuff

    #yith-quick-view-content {
        .edgtf-owl-slider .owl-nav {
            .owl-prev,
            .owl-next {
                color: $additional-text-color;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .product {
            margin: 0;
        }

        .product-type-grouped {
            .summary {
                p.price {
                    margin: 0 0 30px;
                }
            }
        }

        .product-type-external {
            .summary {
                .edgtf-single-product-share-wish {
                    margin-top: 47px;
                }
            }
        }

        .product-type-variable {
            .summary {
                p.price {
                    margin: 0 0 30px;
                }
            }

            form {
                .select2-container--default {
                    float: none;
                }

                .single_variation_wrap {
                    margin: 5px 0 0;
                }
            }
        }

        .images,
        .summary {
            position: relative;
            float: left;
            width: 50%;
            box-sizing: border-box;
        }

        .images {
            a, img {
                display: block;
            }
        }

        .summary {
            padding: 50px;

            .single_add_to_cart_button {
            }

            .variations {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0 0 20px;
                border: none;
                padding: 0;

                select {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    background-color: #fff;
                    border: 1px solid #e1e1e1;
                    box-sizing: border-box;
                    line-height: 50px;
                    height: 50px;
                    padding: 0 16px 0 16px;
                    margin: 0 -4px 0 0;
                    color: $default-text-color;
                    font-weight: 300;
                    font-size: 13px;
                    font-family: $default-text-font;
                }

                tr {
                    border: 0;
                    width: 100%;

                    &:last-child {
                        td {
                            padding: 0;
                        }
                    }
                }

                td {
                    padding: 0 0 20px 0;
                    text-align: left;
                    display: table-cell !important;
                }

                td.label {
                    width: 65px;

                    label {
                        display: inline-block;
                    }
                }

                .select2-container {
                    min-width: 180px;
                    width: 100% !important;
                }

                .reset_variations {
                    color: $default-heading-color;
                    position: relative;
                    top: -5px;
                    right: -10px;
                }
            }

            .single_variation_wrap {
                margin: 35px 0 0;

                .single_variation {
                    margin: 0 0 35px;
                }
            }

            table.group_table {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0;
                border: none;
                padding: 0;

                tbody {
                    display: block;
                }

                tr {
                    border: 1px solid $default-border-color;
                    display: table;
                    margin-bottom: 13px;
                    width: 100%;
                    background-color: #fff;
                }

                td {
                    box-sizing: border-box;
                    padding: 0 10px;
                    text-align: left;
                    display: inline-block;

                    &:first-child {
                        padding: 0;
                    }

                    &.label {
                        text-align: left;
                        vertical-align: middle;
                    }

                    &.price {
                        display: table-cell;
                        font-size: 16px;
                        margin: 0;
                        text-align: center;
                        vertical-align: middle;

                    }
                }

                .edgtf-quantity-buttons {
                    label {
                        display: none;
                    }
                }

                a {
                    color: $default-text-color;

                    &:hover {
                        color: $default-heading-color;
                    }
                }
            }

            .edgtf-yith-product-title {
                //display: block;
                //margin: 0 0 7px;
                //text-transform: uppercase;
            }

            div[itemprop="offers"] {
                position: relative;
                display: block;
            }

            .price {
                @include edgtfWooCommerceSingleProductPriceStyle();

                font-size: 21px;
                margin: 10px 0 0;
            }

            .woocommerce-product-rating {
                margin-top: 20px;
            }

            div[itemprop="description"],
            .woocommerce-product-details__short-description {
                @include edgtfRelativeHolderLayout();

                margin-top: 17px;

                p {
                    margin: 0;
                }
            }

            form.cart {
                @include edgtfRelativeHolderLayout();

                margin-top: 29px;

                label {
                    display: none;
                }
            }

            .edgtf-single-product-share-wish {
                position: relative;
                display: inline-block;
                width: 100%;
                vertical-align: middle;
                margin: 0px;

                .yith-wcwl-wishlistexistsbrowse,
                .yith-wcwl-wishlistaddedbrowse {
                    a {
                        &:after {
                            color: $first-main-color;
                        }
                    }
                }

                .clear {
                    display: none;
                }

                > div {
                    padding: 0 20px;

                    &:after {
                        content: "|";
                        color: #d3d3d3;
                        position: absolute;
                        left: 0;
                        top: 0;
                    }

                    &:first-child {
                        padding-left: 0;

                        &:after {
                            display: none;
                        }
                    }

                    &:last-child {
                        padding-right: 0;
                    }
                }

                .edgtf-woo-social-share-holder {
                    float: left;
                    position: relative;

                    > * {
                        display: table-cell;
                        vertical-align: top;
                        line-height: 22px;
                    }

                    > span {
                        font-size: 12px;
                        text-transform: uppercase;
                        letter-spacing: 2px;
                        color: $first-main-color;
                        padding: 0 9px 0 0;
                    }
                }
            }

            .yith-wcwl-add-to-wishlist {
                width: auto;
                background-color: transparent;
                float: left;

                .yith-wcwl-add-button,
                .yith-wcwl-wishlistaddedbrowse,
                .yith-wcwl-wishlistexistsbrowse {
                    a {
                        //font-size: 12px;
                        //line-height: inherit;
                        //color: $first-main-color;
                        //text-transform: uppercase;
                        //letter-spacing: 2px;
                        //line-height: 22px;

                        //&:after {
                        //    content: "\e089";
                        //    font-family: "Ionicons";
                        //    font-size: 17px;
                        //    float: left;
                        //    line-height: 21px;
                        //    padding-right: 10px;
                        //}
                    }
                }

                .yith-wcwl-add-button {
                    a {
                        &:after {
                            content: "\f141";
                            font-family: "Ionicons";
                            font-size: 17px;
                            float: left;
                            line-height: 21px;
                            padding-right: 10px;
                        }
                    }
                }
            }

            p.stock.out-of-stock,
            p.stock.in-stock {
                color: $default-heading-color;
                font-weight: bold;
                margin-top: 10px;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // close button

    #yith-quick-view-close {
        top: 15px;
        right: 15px;
        width: 30px;
        height: 30px;
        font-size: 0;
        line-height: 30px;
        font-weight: 400;
        text-align: center;
        opacity: 1;
        border: 0;
        color: $default-heading-color;

        &:before {
            position: relative;
            display: inline-block;
            vertical-align: top;
            font-family: 'Ionicons';
            content: "\f404";
            font-size: 30px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        &:hover {
            color: $first-main-color;
        }
    }
}

/* ==========================================================================
   YITH Quick View  styles - end
   ========================================================================== */