////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// popup message

#yith-wcwl-popup-message {
    border: 2px solid $default-border-color;
    border-radius: 0;
    line-height: 52px;
    margin: 0 !important; //absolute centering over added item

    @include edgtfTransform(translate(-50%, -50%));

    padding: 0 21px;
    position: absolute; //absolute centering over added item
    transform-origin: 0 0;
    -webkit-transform-origin: 0 0;

    #yith-wcwl-message {
        @include edgtfHeading5();

        line-height: 52px;

        &:before {
            content: "\f122";
            font-family: 'Ionicons';
            position: relative;
            right: 7px;
            top: 2px;
        }
    }

    //vanishing effect
    &.edgtf-wishlist-vanish-out {
        @include edgtfAnimation(edgtfWishlistvanishOut 1s both);
    }

    @keyframes edgtfWishlistvanishOut {
        0% {
            opacity: 1;

            @include edgtfTransform(scale(1, 1) translate(-50%, -50%));

            -webkit-filter: blur(0px);
            filter: blur(0px);
        }

        100% {
            opacity: 0;

            @include edgtfTransform(scale(2, 2) translate(-50%, -50%));

            -webkit-filter: blur(10px);
            filter: blur(10px);
        }
    }
}

