////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// button in lists

li.product,
.edgtf-pli,
.edgtf-ppi,
.edgtf-plc-holder {
    .yith-wcwl-add-to-wishlist {
        //position: relative;
        display: block;
        float: left;
        height: $product-list-button-size;
        margin: 0;
        width: $product-list-button-size;
        //vertical-align: middle;
        //margin: 0;
        //background-color: #555555;
        //width: 26px;
        //text-align: center;

        > div {
            display: inline-block;
            height: $product-list-button-size;
            position: absolute;
            width: $product-list-button-size;
        }

        ~ div {
            display: none;
        }
    }

    .yith-wcwl-add-button,
    .yith-wcwl-wishlistaddedbrowse,
    .yith-wcwl-wishlistexistsbrowse {
        a {
            font-size: 0px !important;
        }
    }

    .yith-wcwl-add-button {
        .ajax-loading {
            display: none !important;
        }

        a {
            @include edgtfWooCommerceButtonsStyle('\f141');
        }
    }

    .yith-wcwl-wishlistaddedbrowse,
    .yith-wcwl-wishlistexistsbrowse {
        @include edgtfWooCommerceButtonsStyle('\f122');
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// table

#yith-wcwl-form {
    .wishlist-title {
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        background-color: transparent;
        border: 2px solid $default-border-color;
        display: block;
        line-height: 48px;
        list-style: none;
        margin: 50px 0 30px;
        padding: 15px 30px;
        position: relative;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 50px 0 100px;

        thead {
            display: none;
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 2px solid $default-border-color;

                td {
                    padding: 25px 0;
                    vertical-align: middle;

                    &.product-remove {
                        padding-left: 10px;
                        padding-right: 10px;

                        a {
                            color: $default-text-color;
                            font-size: 0;
                            line-height: 1;

                            &:after {
                                color: inherit;
                                content: "\f404";
                                display: inline-block;
                                font-family: "Ionicons";
                                font-size: 25px;
                                font-style: normal;
                                font-variant: normal;
                                font-weight: normal;
                                line-height: inherit;
                                text-transform: none;
                                vertical-align: middle;
                                speak: none;
                                text-rendering: auto;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                            &:hover {
                                color: $first-main-color;
                            }
                        }
                    }

                    &.product-thumbnail {
                        padding-right: 10px;
                        width: 86px;

                        a, img {
                            display: block;
                        }
                    }

                    &.product-name {
                        a {
                            @include edgtfHeading4();
                        }

                        //.yith-wcqv-button {
                        //}
                    }

                    &.product-price {
                        ins {
                            text-decoration: none;
                        }
                    }

                    &.product-stock-status span {
                        color: $default-text-color !important;
                    }

                    &.product-add-to-cart {
                        a {
                            @include edgtfButtonSimpleStyle();

                            &:hover {
                                @include edgtfButtonTransparentHoverColor();
                            }
                        }
                    }
                }
            }
        }

        .yith-wcwl-share {
            margin: 0;
        }
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

.edgtf-single-product-summary {
    .yith-wcwl-add-to-wishlist {
        .feedback {
            @include edgtfHeading5();
        }
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// widget

.edgtf-wishlist-widget-holder {
    @include edgtfOpenerStyle();

    .edgtf-wishlist-items-number {
        display: inline-block;
        font-size: 11px;
    }
}

.edgtf-light-header {
    .edgtf-menu-area {
        .edgtf-wishlist-widget-holder {
            a {
                color: $header-light-color;

                .edgtf-wishlist-items-number {
                    color: $header-light-color;
                }

                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
}

.edgtf-dark-header {
    .edgtf-menu-area {
        .edgtf-wishlist-widget-holder {
            a {
                color: $header-dark-color;

                .edgtf-wishlist-items-number {
                    color: $header-dark-color;
                }

                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
}

