/* ==========================================================================
   WooCommerce Variation styles - begin
   ========================================================================== */

.edgtf-woocommerce-page .edgtf-content {
    .variations {
        border: none;
        display: inline-block;
        margin: 0;
        padding: 0;
        position: relative;
        vertical-align: middle;

        tr {
            border: 0;
            display: block;
            padding: 0 0 20px;

            &:last-child {
                margin: 0;
            }
        }

        td {
            padding: 0;
            text-align: initial;

            &.label {
                padding-right: 10px;
                width: 65px;
            }

            &.value {
                white-space: nowrap;

                select {
                    min-width: 200px;
                    width: auto;
                }
            }
        }

        .reset_variations {
            color: $default-heading-color;
            margin: 0 0 0 15px;
        }
    }

    .single_variation_wrap {
        margin: 35px 0 0;

        .single_variation {
            margin: 0 0 35px;
        }
    }

    table.group_table {
        border: none;
        display: inline-block;
        margin: 0 0 50px 0;
        padding: 0;
        position: relative;
        vertical-align: middle;

        tbody {
            display: block;
        }

        tr {
            border: 0;
            display: block;
            margin: 0;
            padding: 0 0 10px;
        }

        td {
            padding: 0 17px 0 0;
            text-align: left;
            vertical-align: middle;

            &.price {
                display: table-cell;
                vertical-align: middle;
            }

            &:last-child {
                padding: 0;
            }
        }

        .edgtf-quantity-buttons {
            margin: 0;
        }

        a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Variation styles - end
   ========================================================================== */