///* ==========================================================================
//   Product Pair shortcode styles - begin
//   ========================================================================== */

.edgtf-pp-holder {
    @include edgtfRelativeHolderLayout();

    .edgtf-pp-holder-inner {
        @include edgtfTableLayout();

        clear: both;
        position: relative;
        z-index: 1;

        .edgtf-no-posts {
            margin: 20px 15px 40px 15px;
        }
    }

    .edgtf-ppi-outer {
        @include edgtfTableCellLayout();
    }

    .edgtf-ppi-outer:first-child {
        .edgtf-ppi {
            padding-right: 15px;
        }
    }

    .edgtf-ppi-outer:last-child {
        .edgtf-ppi {
            padding-left: 15px;
        }
    }

    &.edgtf-offset-left {
        .edgtf-ppi-outer:first-child {
            padding-top: 80px;
        }
    }

    &.edgtf-offset-right {
        .edgtf-ppi-outer:last-child {
            padding-top: 80px;
        }
    }

    .edgtf-ppi {
        .edgtf-ppi-title {
            margin: 0;
        }

        .edgtf-ppi-category {
            margin: 0px;
        }

        .edgtf-ppi-excerpt {
            margin: 0px;
        }

        .edgtf-ppi-rating-holder {
            @include edgtfRelativeHolderLayout();

            line-height: 1;
            margin: 3px 0 6px;
        }

        .edgtf-ppi-rating {
            @include edgtfWooCommerceRatingsStyle();
        }

        .edgtf-ppi-price {
            margin-top: 4px;

            .amount {
                @include edgtfWooCommercePriceStyle();
            }

            del .amount {
                margin-right: 5px;
                text-decoration: line-through;
            }

            ins {
                .amount {
                    margin-left: 5px;

                    @include edgtfWooCommercePriceStyle('discount');
                }
            }
        }

        .edgtf-ppi-add-to-cart {
            display: inline-block;
            font-size: 0px;
            vertical-align: middle;
            background-color: #fff;

            @include edgtfTransition(opacity 0.3s ease);

            .add_to_cart_button {
                @include edgtfWooCommerceButtonsStyle('\f370');
            }

            .edgtf-read-more-button {

            }

            .added {
                display: none;
            }

            .added_to_cart.wc-forward {
                @include edgtfWooCommerceButtonsStyle('\f13a');
            }
        }
    }

    .edgtf-ppi-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .edgtf-ppi-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .edgtf-ppi-onsale,
            .edgtf-ppi-out-of-stock,
            .edgtf-ppi-new-product {
                @include edgtfWooCommerceLabelStyle();
            }

            .edgtf-ppi-new-product {
                @include edgtfWooCommerceLabelStyle('left');
            }
        }

        .edgtf-ppi-text {
            bottom: 0;
            box-sizing: border-box;
            display: inline-block;
            left: 50%;
            opacity: 0;
            position: absolute;
            white-space: nowrap;
            z-index: 10;

            @include edgtfTransition(all .3s);
            @include edgtfTransform(translateX(-50%));
        }

        .edgtf-ppi-text-inner {
            font-size: 0; //to remove space between inline-block items

            .edgtf-ppi-category,
            .edgtf-ppi-excerpt,
            .edgtf-ppi-rating,
            .edgtf-ppi-price {
                color: $default-text-color;
            }
        }

        .edgtf-ppi-link {
            position: absolute;
            top: 0;
            left: 0;
            display: inline-block;
            width: 100%;
            height: 100%;
        }
    }

    .edgtf-ppi-text-wrapper.edgtf-no-rating-price {
        padding-bottom: 25px;
    }

    .edgtf-ppi-buttons {
        position: absolute;
        text-align: center;
        top: 50%;
        width: 100%;
        z-index: 3;

        @include edgtfTransform(translateY(-50%));
    }

    .edgtf-ppi-add-to-cart {
        display: inline-block;
        vertical-align: middle;
    }

    .edgtf-ppi-buttons {
        opacity: 0;
    }

    .edgtf-ppi-link {
        background-color: $default-shading-color;
        opacity: 0;

        //@include edgtfBorderWithImage();
    }

    .edgtf-ppi-text-wrapper {
        @include edgtfRelativeHolderLayout();

        margin: 26px 0 12px;
        text-align: center;
    }

    // hover handling
    .edgtf-ppi-inner {
        &:hover {
            .edgtf-ppi-link,
            .edgtf-ppi-buttons {
                opacity: 1;

                @include edgtfTransition(all .6s cubic-bezier(0.12, 0.68, 0.43, 0.95));
            }
        }
    }

    &.edgtf-product-info-light {
        .edgtf-ppi-inner {
            .edgtf-ppi-text-inner {
                .edgtf-ppi-title,
                .edgtf-ppi-category,
                .edgtf-ppi-excerpt,
                .edgtf-ppi-rating {
                    color: #fff;
                }

                .edgtf-ppi-price {
                    color: #fff;

                    del {
                        color: #ccc;
                    }
                }
            }
        }
    }

    &.edgtf-product-info-dark {
        .edgtf-ppi-inner {
            .edgtf-pp-text-inner {
                .edgtf-ppi-title,
                .edgtf-ppi-category,
                .edgtf-ppi-excerpt,
                .edgtf-ppi-rating {
                    color: $default-heading-color;
                }

                .edgtf-ppi-price {
                    color: $default-heading-color;
                }
            }
        }
    }

    &.edgtf-border-pattern {
        .edgtf-ppi-link {
            @include edgtfBorderWithImage();
        }
    }
}

/* ==========================================================================
   Product Pair shortcode styles - end
   ========================================================================== */
