/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.edgtf-woocommerce-page {
    &.woocommerce-cart {
        .woocommerce-message {
            box-sizing: border-box;
            width: calc(65% - 30px);
        }
    }

    .woocommerce-cart-form {
        box-sizing: border-box;
        min-height: 50vh;
        padding-right: 30px;
        width: 65%;
    }

    table.cart {
        thead {
            tr {
                border: 0;
                border-bottom: 2px solid $default-border-color;

                th {
                    color: $default-heading-color;
                    padding: 10px 0;
                    text-align: left;
                    text-transform: lowercase;
                }
            }
        }

        tbody {
            tr {
                border: 0;
                border-bottom: 2px solid $default-border-color;
            }
        }

        tr.cart_item {
            td {
                padding: 25px 0;
                text-align: inherit;
                vertical-align: middle;

                &.product-remove {
                    padding-left: 10px;
                    padding-right: 10px;

                    a {
                        color: $default-text-color;
                        font-size: 0;
                        line-height: 1;

                        &:after {
                            color: inherit;
                            content: "\f404";
                            display: inline-block;
                            font-family: "Ionicons";
                            font-size: 25px;
                            font-style: normal;
                            font-variant: normal;
                            font-weight: normal;
                            line-height: inherit;
                            text-transform: none;
                            vertical-align: middle;
                            speak: none;
                            text-rendering: auto;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }

                &.product-thumbnail {
                    padding-right: 10px;
                    width: 86px;

                    a,
                    img {
                        display: block;
                    }
                }

                &.product-name {
                    @include edgtfHeading4();
                }

                .variation {

                    > * {
                        display: inline-block;
                        margin: 0;
                        padding: 0;
                        vertical-align: middle;
                    }

                    p {
                        margin: 0;
                    }
                }
            }
        }

        td.actions {
            padding: 35px 0;
            text-align: inherit;

            .coupon {
                display: inline-block;
                float: left;
                position: relative;
                vertical-align: middle;

                label {
                    display: none;
                }

                input {
                    margin: 0 22px 0 0;
                }
            }

            > input,
              button {
                float: right;
            }

            .button {
                @include edgtfButtonSimpleStyle();

                line-height: 54px;

                &:hover {
                    @include edgtfButtonTransparentHoverColor();
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // cross and up selling

    .cart-collaterals {
        h2 {
            display: block;
            margin: 0 0 50px;
            position: relative;
        }
    }

    .cross-sells {
        margin: 120px 0 0;

        > h2 {
            display: block;
            margin: 0 0 50px;
            text-align: center;
        }

        li {
            margin: 0 !important;
        }
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// right, cart totals

.edgtf-woocommerce-page {
    .cart_totals {
        background: $additional-background-color;
        position: absolute;
        right: 0;
        top: 0;
        width: 35%;

        h2 {
            @include edgtfHeading3();

            margin: 0;
            padding: 40px;
        }

        table.shop_table {

            tr {
                border: 0;

            }

            tr.order-total {
                border-top: 2px solid $default-border-color;

                th, td {
                    font-weight: bold;
                    padding-bottom: 40px;
                    padding-top: 40px;
                }
            }

            th {
                @include edgtfHeading5();

                border: 0;
                padding: 17px 25px 17px 40px;
                text-align: left;
                width: 40%;
            }

            td {
                border: 0;
                box-sizing: border-box;
                padding: 17px 40px 17px 25px;
                text-align: left;
                width: 60%;
            }
        }

        tr.shipping {
            #shipping_method {
                list-style: none;
                margin: 0 0 7px;

                li {
                    margin: 3px 0;
                    padding: 0;

                    > * {
                        display: inline-block;
                        margin: 0;
                        vertical-align: middle;
                    }
                }
            }

            td {
                section {
                    padding-right: 40px;
                    width: 100%;
                }
            }
        }

        .woocommerce-shipping-calculator {
            width: 150px;

            .shipping-calculator-button {
                display: block;
                font-weight: bold;
                letter-spacing: 0.5px;
                margin: 0 0 10px;
                text-transform: lowercase;
            }

            p {
                margin: 0;
            }

            button {
                @include edgtfButtonSimpleStyle(!important);

                line-height: 52px !important;
            }
        }

        .wc-proceed-to-checkout {
            padding: 0 40px 40px 40px;
            text-align: center;

            .button {
                padding-left: 0;
                padding-right: 0;
                width: 100%;
            }
        }
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// empty cart

.edgtf-woocommerce-page {
    .cart-empty {
        @include edgtfHeading3();

        margin: 80px 0 120px 0;
        position: relative;
        text-align: center;
    }

    .return-to-shop {
        display: block;
        margin: 0 0 120px;
        position: relative;
        text-align: center;
    }
}

/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */