.edgtf-product-info-carousel {
    position: relative;

    .edgtf-product-info-carousel-images {
        box-sizing: border-box;
        left: 0;
        padding-left: 45px;
        position: absolute;
        top: 50%;
        width: 50%;

        @include edgtfTransform(translate3d(0,-50%,0));
    }

    &.edgtf-pic-white-skin-nav {
        .edgtf-owl-slider {
            .owl-nav {
                .owl-next,
                .owl-prev {
                    color: #ffffff;
                }
            }
        }
    }

    .edgtf-owl-slider {
        .owl-nav {
            .owl-prev {
                left: 75px;

                .edgtf-prev-icon {
                    @include edgtfTransition(all .2s cubic-bezier(.12,.68,.43,.95));
                }

                &:hover {
                    .edgtf-prev-icon {
                        @include edgtfTransform(translateX(-4px));
                    }
                }
            }

            .owl-next {
                .edgtf-next-icon {
                    @include edgtfTransition(all .2s cubic-bezier(.12,.68,.43,.95));
                }

                &:hover {
                    .edgtf-next-icon {
                        @include edgtfTransform(translateX(4px));
                    }
                }
            }
        }
    }

    .edgtf-pic-content {
        padding: 60px 0;

        .edgtf-pic-content-inner {
            background: #ffffff;
            padding: 85px 70px 85px calc(50% + 70px);
            box-sizing: border-box;

            @include edgtfBorderWithImage();

            .edgtf-pic-price {
                @include edgtfWooCommercePriceStyle();

                margin: 0 0 7px 11px;
            }

            .edgtf-pic-title {
                margin: 0 0 5px 0;
            }

            .edgtf-pic-rating {
                @include edgtfWooCommerceRatingsStyle();

                margin-bottom: 22px;
            }

            .edgtf-pic-excerpt {
                margin-bottom: 44px;
            }

            .edgtf-pic-add-to-cart {
                position: relative;

                .added {
                    opacity: 0;
                }

                .edgtf-btn {
                    @include edgtfButtonSimpleStyle();

                    &:before { 
                        content: "";
                        position: absolute;
                        bottom: -10px;
                        left: 0;
                        width: 32px;
                        height: 2px;
                        background: currentColor;
                        transform-origin: left;
                        @include edgtfTransition(width .2s cubic-bezier(.12,.68,.43,.95));
                    }

                    &:hover {
                        &:before {
                            width:43px;
                        }
                    }
                }

                .added_to_cart {
                    @include edgtfButtonDefaultStyle();
                    @include edgtfButtonOutlineColor();
                    @include edgtfButtonMediumParams();

                    //@include edgtfButtonSolidColor();

                    left: 0;
                    position: absolute;
                    white-space: nowrap;
                    z-index: 3;

                    &:hover {
                        @include edgtfButtonOutlineHoverColor();
                    }
                }
            }
        }
    }
}