/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {
    .edgtf-quantity-buttons {
        border: 2px solid $default-border-color;
        display: inline-block;
        position: relative;
        vertical-align: middle;

        label {
            display: inline-block;
            padding: 0 20px;
        }

        .edgtf-quantity-buttons-inner {
            display: inline-block;
            position: relative;
        }

        .edgtf-quantity-input,
        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            background-color: transparent;
            box-sizing: border-box;
            color: $default-text-color;
            display: inline-block;
            text-align: center;
            vertical-align: middle;
        }

        .edgtf-quantity-input {
            border: 0;
            font-family: inherit;
            font-size: 15px;
            height: 48px;
            line-height: 48px;
            margin: 0 26px;
            outline: 0;
            padding: 0;
            position: relative;
            width: 40px;
        }

        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            color: $additional-text-color;
            cursor: pointer;
            height: 48px;
            line-height: 48px;
            position: absolute;
            top: 0;
            width: 26px;

            @include edgtfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .edgtf-quantity-plus {
            right: 0;
            text-align: left;
        }

        .edgtf-quantity-minus {
            left: 0;
            text-align: right;
        }
    }
}

.woocommerce-page .edgtf-content,
div.woocommerce {
    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        background-color: transparent;
        border: 2px solid $default-border-color;
        box-sizing: border-box;
        color: $default-text-color;
        font-family: inherit;
        font-size: inherit;
        font-weight: inherit;
        line-height: 48px;
        margin: 0 0 14px;
        outline: 0;
        padding: 0 18px;
        position: relative;
        width: 100%;

        &:focus {
            border-color: $default-heading-color;
        }

        @include edgtfPlaceholder {
            color: inherit;
        }

        -webkit-appearance: none;
    }

    .coupon {
        input[type="text"] {
            width: 200px;
        }
    }
}

/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */