/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
.edgtf-woocommerce-page.edgtf-woocommerce-order-tracking,
div.woocommerce {
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.edgtf-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include edgtfButtonDefaultStyle();
        @include edgtfButtonMediumParams();
        @include edgtfButtonSolidColor();

        cursor: pointer;
        z-index: 3;

        &:hover {
            @include edgtfButtonSolidHoverColor();
        }
    }

    a.button.added {
        display: none;
    }
}

/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */