/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $list-item-padding: 5px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include edgtfHeading4();

        font-size: 18px;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        .price_slider_wrapper {
            .ui-widget-content {
                position: relative;
                height: 4px;
                background-color: $additional-background-color;

                .ui-slider-handle {
                    position: absolute;
                    top: -6px;
                    height: 10px;
                    width: 1px;
                    background-color: $default-heading-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;

                    &:last-child {
                        @include edgtfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $default-heading-color;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 33px 0 0;

            .button {
                float: right;

                @include edgtfButtonDefaultStyle();
                @include edgtfButtonSimpleStyle();

                line-height: 1.73em;

                &:hover {
                    @include edgtfButtonSimpleHoverStyle();
                }
            }

            .price_label {
                float: left;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: $list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart,
    &.widget_recent_reviews {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include edgtfHeading4();

            font-size: 18px;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 0;

            &:after {
                content: "\f404";
                display: block;
                font-family: 'Ionicons';
                font-size: 30px;
                line-height: 30px;
                position: relative;
            }
        }

        .variation {
            @include edgtfRelativeHolderLayout();

            > * {
                display: inline-block;
                vertical-align: middle;
                margin: 0;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include edgtfButtonSimpleStyle();

                &:hover {
                    @include edgtfButtonSimpleHoverStyle();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */